import { useContext } from 'react'

import { useLogRestriction } from '../../utils/myHookFunctions'
import { AuthContext } from '../../utils/authorization'

function PwdResetStage3(){
    const userContext = useContext(AuthContext)
    useLogRestriction(userContext.user) //at logged in state redirection

    return (
        <div>
            <h4>Your Forgotten Password has been resetted!</h4>
            <p>A minute later you can use your new password!</p>
        </div>
    )
}

export default PwdResetStage3