import { useState } from 'react'
import { useLocation, useHistory } from 'react-router-dom'

export const useFormUsageActions = (initValues, callbackAtSending)=>{

    const [ values, setFormValues ] = useState(initValues)
    const onChange = (event) =>{
        setFormValues({
            ...values, 
            [event.target.name]: event.target.value
        })
    }
    const onSubmit = (event)=>{
        event.preventDefault();
        callbackAtSending();
    }
    const clearFields = ()=>{
        setFormValues(initValues)
    }
    return {
        values,
        onSubmit,
        onChange,
        clearFields
    }
}

export const useFormErrorActions = ()=>{

    //initial value for compatibliy is always the same
    const [ errors, setSomeError ] = useState({ field: [], message: [] })
    //datas are comming from client validation module!
    //or server result - in case of validation error
    const setValidationErrors = (err)=>{
        setSomeError({ field: err.field, message: err.message })
    }
    const setGeneralErrors = (errMsg)=>{
        setSomeError({ field: [], message: [ errMsg ] })
    }
    const resetErrors = ()=>{
        setSomeError({ field: [], message: [] })
    }
    return {
        errors,
        resetErrors,
        setValidationErrors,
        setGeneralErrors
    }
}


export const usePathGetter = ()=>{
    const actLocat = useLocation()
    return actLocat.pathname.split('/')
}

export const useLogRestriction = (contextRestriction, destinAtRestrict)=>{
    const history = useHistory()
    if(contextRestriction){
        history.push(destinAtRestrict)
    }
}